import axios from "axios";
import { Component, ReactNode } from "react";
import { NavLink, RouteComponentProps } from "react-router-dom";
import config from "../../../Configs/Configs";
import productModel from "../../../Modules/productModel";
import "./Buy.css";
import setHeaders from "../../../Services/setHeaders";
import Similar from "../Similar/Similar";
import Sales from "../../Home/Sales/Sales";
import { Button, TextField } from "@material-ui/core";
import Cart from "../Cart/Cart";
import BuyModule from "../BuyModule/BuyModule";
interface RouterParams {
    image: string;
    id: string;
}
interface BuyProps extends RouteComponentProps<RouterParams> {
}
interface BuyState {
    product: productModel;
    count: number,
    similarProduct: productModel[],
    isModuleOpen: boolean

}

class Buy extends Component<BuyProps, BuyState> {

    public constructor(props: BuyProps) {
        super(props);
        this.state = {
            product: null,
            count: 0,
            similarProduct: null,
            isModuleOpen: false

        };
    }
    public async componentDidMount() {
        const headers = setHeaders();
        const response = await axios.get<productModel>(config.urls.product + this.props.match.params.id, { headers: headers });
        this.setState({ product: response.data });

    }
    public render(): JSX.Element {
        return (
            <div className="Buy">
                <div className="images">
                    <img src={config.urls.product + "images/" + this.props.match.params.image} />
                    <img src={config.urls.product + "images/" + this.props.match.params.image} />
                    <img src={config.urls.product + "images/" + this.props.match.params.image} />
                </div>
                <div className="title">
                    <div className="title-price">
                        <div>
                            <h3>מחיר</h3>
                        </div>
                        <div className="title-price-child2">
                            <h3>{this.state.product?.price}שח</h3>
                        </div>
                    </div>
                    <div className="paymentCart">
                        <NavLink to={'/payment/' + this.state.product?.image + "&&" + this.state.product?.id}>רכישה</NavLink>
                        <NavLink to='/cart'>הוסף לסל</NavLink>
                    </div>
                    <div className="description">
                        <h5>:תיאור המוצר</h5>
                        <p>
                            {this.state.product?.description}
                        </p>
                        <br />
                        <br />
                    </div>

                    <div>
                        <h3>מבצעים מיוחדים</h3>
                        <Sales />
                    </div>
                    <div>
                        <h3>מוצרים דומים</h3>
                        <Similar />
                    </div>
                </div>

                <div>

                </div>




            </div>
        );
    }
}

export default Buy;
